import React, { Component } from 'react';

import { Grid, Row, Column, Modal } from '../../lib-web';
import Styles from './styles';

import Children from 'react-children-utilities';

class SimActivation extends Component {
  constructor() {
    super();

    this.state = {
      showSimReplacement: false,
      visibleScreens: 'default',
      errorDetails: null,
      containerFound: false,
      errors: {}
    };

    this.axios = require('axios');

    this.backFromSimReplacement = this.backFromSimReplacement.bind(this);
    this.showSimReplacement = this.showSimReplacement.bind(this);
    this.goToScreen = this.goToScreen.bind(this);
    this.onPhoneChange = this.onPhoneChange.bind(this);
    this.onOTPChange = this.onOTPChange.bind(this);
    this.onSimNumberChange = this.onSimNumberChange.bind(this);
    this.submitSimReplacentPhone = this.submitSimReplacentPhone.bind(this);
    this.submitOTP = this.submitOTP.bind(this);
    this.submitSimNumber = this.submitSimNumber.bind(this);
  }

  backFromSimReplacement(step) {
    switch(step) {
      case 2:
        this.setState({
          'visibleScreens': 'sim-replacement-phone'
        });
        break;
      case 3:
          this.setState({
            'visibleScreens': 'sim-replacement-otp'
          });
          break;
      default:
        this.setState({
          'showSimReplacement': false,
          'visibleScreens': 'default'
        });
    }
  }

  showSimReplacement() {
    this.setState({
      'showSimReplacement': true,
      'visibleScreens': 'sim-replacement-phone one'
    });
  }

  goToScreen(screen) {
    this.setState({
      'visibleScreens': screen
    });
  }

  setCookieValue(cookieName, cookieValue, expiryDays) {
    var d = new Date();
    d.setTime(d.getTime() + (expiryDays * 24 * 60 * 60 * 1000));
    var expires = "expires=" + d.toUTCString();
    document.cookie = cookieName + "=" + cookieValue + ";" + expires + ";path=/;Domain=.optus.com.au";
    return true;
  }

  getCookieValue(cname) {
      var name = cname + "=";
      var decodedCookie = decodeURIComponent(document.cookie);
      var ca = decodedCookie.split(';');
      for (var i = 0; i < ca.length; i++) {
          var c = ca[i];
          while (c.charAt(0) == ' ') {
              c = c.substring(1);
          }
          if (c.indexOf(name) == 0) {
              return c.substring(name.length, c.length);
          }
      }
      return null;
  }

  setErrorState(errorCode) {
    var errorCodes = this.props.errorMessages[0], thisError;
    Object.keys(errorCodes).forEach(function(key) {
      if (errorCodes[key][0].errorCode === errorCode) {
        thisError = errorCodes[key][0];
      }
    });
    this.setState({
      'errorDetails': thisError || errorCode
    });
  }

  submitSimReplacentPhone() {
    var obj = {
      input: this.state.inputPhoneNumber
    };

    // this.props.showLoading(true);
    // check if there have been 3 attempts in the last hour
    var phoneAttempts = this.getCookieValue('submitPhoneNumberAttempts') || '', currentTimestamp = new Date().getTime();
    if (phoneAttempts) {
     var attemptCount = 0;
     phoneAttempts.split(",").map((attempt) => {
       console.log([attempt.split(':')[0], obj.input, attempt.split(':')[0] === obj.input]);
       if (attempt.split(':')[0] === obj.input) {
        if ((currentTimestamp - attempt.split(':')[1]) < (60 * 60000)) {
          attemptCount++;
        }
       }
     });
     if (attemptCount >= 3) {
      return new Promise((resolve, reject) => {
        this.setErrorState("TooManyPhoneNumberAttemptsFrontEnd");
        // reject({ "status": "TooManyPhoneNumberAttemptsFrontEnd" });
      });
     }
    }
    return this.axios.post(this.props.apiUrlSubmitPhoneNumber, { ImplSendOtpInput: { msisdn: obj.input, checkPendingOrder: false } }).then((response) => {
      response = response.data;
      return new Promise((resolve, reject) => {
        // this.props.showLoading(false);
        // this.props.phoneStatus(obj.input, response.ImplSendOtpOutput.status);
        // was this the first lookup for an unrecognized number?
        if (response.ImplSendOtpOutput.status === "MSISDNNotFound") {
          var previousAttempt = false;
          phoneAttempts.split(",").map((attempt) => {
            if (attempt.split(':')[0] === obj.input) {
              previousAttempt = true;
            }
          });
          if (!previousAttempt) {
            this.setCookieValue('submitPhoneNumberAttempts', phoneAttempts + ',' + obj.input + ':' + currentTimestamp);
            // reject({
            //   "status":"MSISDNNotFoundSoft"
            // });
            return;
          }
        }
        this.setCookieValue('submitPhoneNumberAttempts', phoneAttempts + ',' + obj.input + ':' + currentTimestamp);
        if (response.ImplSendOtpOutput.status === 'SUCCESS') {
          this.setState({
            'visibleScreens': 'sim-replacement-otp one'
          });
          // resolve(response.ImplSendOtpOutput.status);
        } else {
          // var errorLookup = this.props.errorMessages[response.ImplSendOtpOutput.status];
          // console.log(['errLookup', errorLookup]);
          this.setErrorState(response.ImplSendOtpOutput.status);
          // reject(response.ImplSendOtpOutput);
        }
      });
    }).catch(function (error) {
      this.setErrorState(error.data.ImplSendOtpOutput.status);
    });
  }

  submitOTP(obj) {
    var obj = {
      input: this.state.inputOTP
    };

    // this.props.showLoading(true);
    console.log(['submitting to', this.props.apiUrlSubmitOtp]);
    return this.axios.post(this.props.apiUrlSubmitOtp, { ImplVerifyOTPRestInput: { msisdn: this.state.inputPhoneNumber, otp: obj.input } }).then((response) => {
      response = response.data;
      return new Promise((resolve, reject) => {
        // this.props.showLoading(false);
        // this.props.otpStatus(obj.input, response.ImplVerifyOtpOutput.otpResult);
        if (response.ImplVerifyOtpOutput.otpResult === 'SUCCESS') {
          this.setState({
            'visibleScreens': 'sim-replacement-sim-number one'
          });
          // resolve(response.ImplVerifyOtpOutput.otpResult);
        } else {
          // var errorLookup = this.props.errorMessages[response.ImplVerifyOtpOutput.otpResult];
          this.setErrorState(response.ImplVerifyOtpOutput.otpResult);
          // reject(response.ImplVerifyOtpOutput);
        }
      });
    }).catch(function (error) {
      this.setErrorState(error.data.ImplVerifyOtpOutput.otpResult);
  });
  }

  submitSimNumber(obj) {
    var obj = {
      input: this.state.inputSimNumber
    };

    // this.props.showLoading(true);
    return this.axios.post(this.props.apiUrlSubmitSimNumber, { ImplSubmitSIMReplacementInput: { msisdn: this.state.inputPhoneNumber, otp: this.state.inputOTP, simNumber: obj.input } }).then((response) => {
      response = response.data;
      return new Promise((resolve, reject) => {
        // this.props.showLoading(false);
        // this.props.simNumberStatus(obj.input, response.ImplSubmitSIMReplacementOutput.status);
        if (response.ImplSubmitSIMReplacementOutput.status === 'SUCCESS') {
          this.setState({
            'visibleScreens': 'sim-replacement-success one'
          });
          // this.props.toNumber(response.ImplSubmitSIMReplacementOutput.transactionId);
          // resolve(response);
        } else {
          // var errorLookup = this.props.errorMessages[response.ImplSubmitSIMReplacementOutput.status];
          this.setErrorState(response.ImplSubmitSIMReplacementOutput.status);
          // reject(response.ImplSubmitSIMReplacementOutput);
        }
      });
    }).catch(function (error) {
      this.setErrorState(error.data.ImplSubmitSIMReplacementOutput.status);
  });
  }

  recursiveMap(children, fn) {
    return React.Children.map(children, child => {
      if (!React.isValidElement(child)) {
        return child;
      }
  
      if (child.props.children) {
        child = React.cloneElement(child, {
          children: recursiveMap(child.props.children, fn)
        });
      }
  
      return fn(child);
    });
  }

  onPhoneChange(event) {
    this.setState({
      'inputPhoneNumber': event.target.value
    });
  }
  onOTPChange(event) {
    this.setState({
      'inputOTP': event.target.value
    });
  }
  onSimNumberChange(event) {
    this.setState({
      'inputSimNumber': event.target.value
    });
  }

  render() {
    let allFormSteps = 1;
    var wrappedChildren = Children.deepMap(this.props.children, function(child) {
      var extraChildProps = {};
      if (child && child.props) {
        if (child.props.reactComponent === 'DynamicScreenItem') {
          extraChildProps.showScreen = this.goToScreen;
          extraChildProps.visibleScreens = this.state.visibleScreens;
        }
        if (((child.props && child.props['cta-1']) ? child.props['cta-1'][0].functionalCtaId : '') === 'goToSimReplacement') {
          extraChildProps.onClick = () => { this.showSimReplacement(child.props['cta-1'][0].text) };
          extraChildProps.text = child.props['cta-1'][0].text;
        }
        if (child.type === SimActivation && !this.state.containerFound) {
          extraChildProps.id = 'SimActivationInnerContainer';
        }
        if (child.type === SimActivation && this.state.containerFound) {
          extraChildProps.id = 'SimReplacementInnerContainer';
        }
        if (child.props.id === 'SimActivationInnerContainer') {
          extraChildProps.isVisible = !this.state.showSimReplacement;
        }
        if (child.props.id === 'SimReplacementInnerContainer') {
          extraChildProps.isVisible = this.state.showSimReplacement;
        }
        if (child.props.id === 'submitPhoneInput') {
          extraChildProps.onChange = this.onPhoneChange;
        }
        if (child.props.id === 'submitOTPInput') {
          extraChildProps.onChange = this.onOTPChange;
        }
        if (child.props.id === 'submitSimNumberInput') {
          extraChildProps.onChange = this.onSimNumberChange;
        }
        if (((child.props && child.props['cta-1']) ? child.props['cta-1'][0].functionalCtaId : '') === 'sim-replacement-submit-phone') {
          extraChildProps.onClick = this.submitSimReplacentPhone;
          extraChildProps.prependInput = true;
          extraChildProps.prependInputChange = this.onPhoneChange;
          extraChildProps.prependInputPlaceholder = 'Enter your phone number';
          extraChildProps.text = child.props['cta-1'][0].text;
          extraChildProps.inputError = this.state.errorDetails ? this.state.errorDetails.failureDescription ? this.state.errorDetails.failureDescription : this.state.errorDetails : '';
        }
        if (((child.props && child.props['cta-1']) ? child.props['cta-1'][0].functionalCtaId : '') === 'sim-replacement-submit-otp') {
          extraChildProps.onClick = this.submitOTP;
          extraChildProps.prependInput = true;
          extraChildProps.prependInputChange = this.onOTPChange;
          extraChildProps.prependInputPlaceholder = 'Enter your one-time password';
          extraChildProps.text = child.props['cta-1'][0].text;
          extraChildProps.inputError = this.state.errorDetails ? this.state.errorDetails.failureDescription ? this.state.errorDetails.failureDescription : this.state.errorDetails : '';
        }
        if (((child.props && child.props['cta-1']) ? child.props['cta-1'][0].functionalCtaId : '') === 'sim-replacement-submit-sim-number') {
          extraChildProps.onClick = this.submitSimNumber;
          extraChildProps.prependInput = true;
          extraChildProps.prependInputChange = this.onSimNumberChange;
          extraChildProps.prependInputPlaceholder = 'Enter your SIM number';
          extraChildProps.text = child.props['cta-1'][0].text;
          extraChildProps.inputError = this.state.errorDetails ? this.state.errorDetails.failureDescription ? this.state.errorDetails.failureDescription : this.state.errorDetails : '';
        }
        if (child.props.id === 'sim-replacement-phone') {
          extraChildProps.formType = true;
          extraChildProps.hideDots = false;
          extraChildProps.steps = 4;
          extraChildProps.thisStep = allFormSteps;
          extraChildProps.goBack = this.backFromSimReplacement;
          allFormSteps++;
        }
        if (child.props.id === 'sim-replacement-otp') {
          extraChildProps.formType = true;
          extraChildProps.hideDots = false;
          extraChildProps.steps = 4;
          extraChildProps.thisStep = allFormSteps;
          extraChildProps.goBack = this.backFromSimReplacement;
          allFormSteps++;
        }
        if (child.props.id === 'default') {
          extraChildProps.leftTab = true;
          extraChildProps.leftTabText = "New SIM";
          extraChildProps.leftTabUrl = "default type";
          extraChildProps.rightTab = true;
          extraChildProps.rightTabText = "Replacement";
          extraChildProps.rightTabUrl = "default replacement";
        }
        if (child.props.id === 'type') {
          extraChildProps.leftTab = true;
          extraChildProps.leftTabText = "Postpaid";
          extraChildProps.leftTabUrl = "default type postpaid";
          extraChildProps.rightTab = true;
          extraChildProps.rightTabText = "Prepaid";
          extraChildProps.rightTabUrl = "/activate";
        }
        if (child.props.id === 'replacement') {

        }
        if (child.props.id === 'sim-replacement-sim-number') {
          extraChildProps.formType = true;
          extraChildProps.hideDots = false;
          extraChildProps.steps = 4;
          extraChildProps.thisStep = allFormSteps;
          extraChildProps.goBack = this.backFromSimReplacement;
          allFormSteps++;
        }
        if (child.props.id === 'sim-replacement-success') {
          extraChildProps.showSuccessIcon = true;
        }
        extraChildProps.showScreens = this.goToScreen;
        return React.cloneElement(child, extraChildProps);
      }
  }.bind(this));
  return (
    <Grid>
      <Row>
        <Column sm={12} md={7} lg={7}>
          {!this.state.errorDetails ? '' : (<Modal
              open={!!this.state.errorDetails}
              onClose={() => { this.setState({'errorDetails': null}) }}
              >
              <Styles.CenteredWrapper>
                  <Styles.ErrorIcon></Styles.ErrorIcon>
                  <Styles.ModalHeading>Something went wrong</Styles.ModalHeading>
                  <Styles.ModalText>{this.state.errorDetails.failureDescription ? this.state.errorDetails.failureDescription : this.state.errorDetails}</Styles.ModalText>
                  {!this.state.errorDetails.failureCtaLabel ? '' : (<>
                      <Styles.ModalCTA onClick={() => { this.state.errorDetails.successMessage === 'close' ? this.setState({'errorDetails': null}) : window.location.href = this.state.errorDetails.successMessage}} text={this.state.errorDetails.failureCtaLabel}></Styles.ModalCTA></>)}
              </Styles.CenteredWrapper>
          </Modal>)}
          {wrappedChildren}
        </Column>
      </Row>
    </Grid>
  );
  }
}

export default SimActivation;
