/* istanbul ignore file */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Styles from './styles';
import { Grid, Row, Column, Anchor, Modal } from '../../lib-web';
import moment from 'moment';
import ServiceDetailsHelpers from '../DirectDebitStatus/ServiceDetailsHelper';
import DirectDebitService from '../../amws-core/services/DirectDebitService';

const ColoredLine = ({ color, height }) => (
  <hr
    style={{
      color: color,
      backgroundColor: color,
      height: height,
      margin: 0
    }}
  />
);

class DirectDebitStatus extends Component {
  constructor(props) {
    super(props);
    this.state = {
      directDebitStatus: null,
      billDueDate: ' ',
      dDActive: false,
      customerAccountNo: ' ',
      showServices: false,
      accountServices: [],
      servicesError: ' '
    };
    this.axios = require('axios');
    this.setAccountNumber = this.setAccountNumber.bind(this);
    this.ddService = new DirectDebitService();
  }

  componentDidMount() {
    this.setDdStatus();
    this.setAccountNumber();
    window.addEventListener(
      'DDSTATUSCHANGED',
      this.handleStatusChanged.bind(this)
    );
    window.addEventListener(
      'HIDEDDSTATUS',
      this.hideDDStatusComponent.bind(this)
    );
  }

  showServiceModal = () => {
    this.setState({ showServices: true });
    this.ddService
      .fetchServices(this.props.isLocal)
      .then(response => {
        response = response.data;
        if (response && response.ImplBillingAccountDetailsResponse) {
          var resp = response.ImplBillingAccountDetailsResponse;
          var res = [];
          if (
            resp &&
            resp.billingAccountDetails &&
            resp.billingAccountDetails.length > 0
          ) {
            for (var i = 0; i < resp.billingAccountDetails.length; i++) {
              if (
                resp.billingAccountDetails[i].faId ===
                this.state.customerAccountNo
              ) {
                res = resp.billingAccountDetails[i];
                break;
              }
            }
            if (res && res.subscribers) {
              this.setState({ accountServices: res.subscribers });
            }
          }
        }
      })
      .catch(error => {
        this.setState({
          servicesError: this.props.serviceError
        });
      });
  };

  hideServiceModal = () => {
    this.setState({ showServices: false });
  };

  setDdStatus() {
    this.ddService
      .getDirectDebitStatus(this.props.isLocal)
      .then(response => {
        response = response.data;
        if (response && response.ImplCustomerRoleDetails) {
          if (
            response.ImplCustomerRoleDetails.accountDetails ||
            response.ImplCustomerRoleDetails.cardDetails
          ) {
            this.setState({ directDebitStatus: this.props.statusActiveText });
            var payDetails = response.ImplCustomerRoleDetails.cardDetails
              ? response.ImplCustomerRoleDetails.cardDetails
              : response.ImplCustomerRoleDetails.accountDetails;
            if (payDetails && payDetails.amountDue > 0) {
              var current = payDetails.currentBillDueDate;
              this.setState({ dDActive: true });
              this.setState({
                billDueDate: moment(current).format('DD MMMM YYYY')
              });
            }
          } else {
            this.setState({ directDebitStatus: this.props.statusInactiveText });
          }
        }
      })
      .catch(error => {
        this.setState({
          directDebitStatus: this.props.errorMessage
        });
      });
  }

  setAccountNumber() {
    if (!this.props.isLocal) {
      var accountDetails = this.ddService.fetchBillId();
      if (accountDetails && accountDetails.faId) {
        this.setState({ customerAccountNo: accountDetails.faId });
      }
    } else {
      this.setState({ customerAccountNo: 'XXXXXXXXX' });
    }
  }

  handleStatusChanged() {
    document
      .getElementsByClassName('DirectDebitStatus')[0]
      .setAttribute('style', 'display:block;');
    this.setDdStatus().bind(this);
  }

  hideDDStatusComponent() {
    document
      .getElementsByClassName('DirectDebitStatus')[0]
      .setAttribute('style', 'display:none;');
  }

  render() {
    const formatMentionText = (text, values, regex) => {
      if (!values.length) return text;
      return (
        <div>
          {text.split(regex).reduce((prev, current, i) => {
            if (!i) return [current];

            return prev.concat(
              values.includes(current) ? (
                <Anchor onClick={this.showServiceModal.bind(this)}>
                  account {this.state.customerAccountNo}{' '}
                </Anchor>
              ) : (
                current
              )
            );
          }, [])}
        </div>
      );
    };
    let accountServices = [],
      temp = [],
      services = this.state.accountServices;
    for (var i = 0; i < services.length; i++) {
      temp.push(services[i].subscriberType);
    }
    var mapServices = temp.reduce(function(prev, cur) {
      prev[cur] = (prev[cur] || 0) + 1;
      return prev;
    }, {});
    for (const key of Object.keys(mapServices)) {
      console.log(key, mapServices[key]);
      accountServices.push(
        <Row key={key}>
          <Column sm={3} md={3} lg={3} className="no-padding">
            <Styles.ServiceIcons>
              <p className={ServiceDetailsHelpers(key).icon} />
              <p className="badges">{mapServices[key]}</p>
            </Styles.ServiceIcons>
          </Column>
          <Column sm={9} md={9} lg={9} className="no-padding_service">
            <Styles.ServiceIcons>
              <p className="service-name">{ServiceDetailsHelpers(key).name}</p>
            </Styles.ServiceIcons>
          </Column>
        </Row>
      );
    }
    let accountNumberLabel;
    var activeAccountLabel = this.props.activeAccountLabel;
    var reg = new RegExp(/\[\||(.*?)\||\]/);
    activeAccountLabel = formatMentionText(
      activeAccountLabel,
      ['ACCOUNT_NUMBER'],
      new RegExp(/\[\||(.*?)\||\]/)
    );
    var inactiveAccountLabel = this.props.inactiveAccountLabel;
    inactiveAccountLabel = formatMentionText(
      inactiveAccountLabel,
      ['ACCOUNT_NUMBER'],
      new RegExp(/\[\||(.*?)\||\]/)
    );

    if (this.state.directDebitStatus === this.props.statusActiveText) {
      accountNumberLabel = (
        <Styles.AccountNumberLabel>
          {activeAccountLabel}
        </Styles.AccountNumberLabel>
      );
    } else {
      accountNumberLabel = (
        <Styles.AccountNumberLabel>
          {inactiveAccountLabel}
        </Styles.AccountNumberLabel>
      );
    }
    let renderAccountNumber;
    renderAccountNumber = (
      <div>
        <Styles.Services>
          <Modal
            open={this.state.showServices}
            onClose={this.hideServiceModal}
            showClose={true}
          >
            <Grid>
              <Row>
                <Column sm={12} md={12} lg={12}>
                  <h4>Account Number: {this.state.customerAccountNo}</h4>
                  <ColoredLine color="Black" height={3} />
                  {accountServices}
                </Column>
              </Row>
            </Grid>
          </Modal>
        </Styles.Services>
        <Styles.Account>{accountNumberLabel}</Styles.Account>
      </div>
    );
    let renderDdStatus;
    if (
      this.state.directDebitStatus &&
      this.state.directDebitStatus === this.props.statusActiveText
    ) {
      renderDdStatus = (
        <Styles.Active>
          <p className="ddStatusLabel">{this.props.ddStatusLabel}</p>
          <Styles.status>
            <Styles.SocialLinks>
              <a href="#">
                <Styles.SuccessTick />
              </a>
            </Styles.SocialLinks>
            <Styles.statusActive>
              {this.state.directDebitStatus}
            </Styles.statusActive>
          </Styles.status>
          {renderAccountNumber}
        </Styles.Active>
      );
    } else if (
      this.state.directDebitStatus &&
      this.state.directDebitStatus === this.props.statusInactiveText
    ) {
      renderDdStatus = (
        <Styles.InActive>
          <p className="ddStatusLabel">{this.props.ddStatusLabel}</p>
          <Styles.status>
            <Styles.SocialLinks>
              <a href="#">
                <Styles.ErrorIcon />
              </a>
            </Styles.SocialLinks>
            <Styles.statusInactive>
              {this.state.directDebitStatus}
            </Styles.statusInactive>
          </Styles.status>
          {renderAccountNumber}
        </Styles.InActive>
      );
    } else {
      renderDdStatus = (
        <Styles.status>
          <Styles.statusInactive>
            {this.state.directDebitStatus}
          </Styles.statusInactive>
        </Styles.status>
      );
    }
    const isDDActive = this.state.dDActive;
    let renderBillDueDate;
    if (isDDActive) {
      renderBillDueDate = (
        <p className="billDueDate">
          Your bill Due Date is {this.state.billDueDate}{' '}
        </p>
      );
    }

    return (
      <Grid>
        <Row>
          <Column sm={12} md={12} lg={12}>
            <Styles.DirectDebitStatus>
              <h2>{this.props.ddHeader}</h2>
              <ColoredLine color="Black" height={5} />
              <Styles.status>{renderDdStatus}</Styles.status>
              <Styles.dueDate>{renderBillDueDate}</Styles.dueDate>
            </Styles.DirectDebitStatus>
          </Column>
        </Row>
      </Grid>
    );
  }
}

DirectDebitStatus.defaultProps = {
  className: 'DirectDebitStatus'
};

DirectDebitStatus.propTypes = {
  /** Class name */
  className: PropTypes.node,
  /** Children inside component */
  children: PropTypes.node
};

export default DirectDebitStatus;
