import APIEndPoints from './APIEndPoints';
import SessionStorageTopics from './SessionStorageTopics';
import HelperConstants from '../helpers/HelperConstants';

export default class DirectDebitService {
  constructor() {
    this.axios = require('axios');
    this.getDirectDebitStatus = this.getDirectDebitStatus.bind(this);
    this.fetchServices = this.fetchServices.bind(this);
  }

  getDirectDebitStatus(isLocal) {
    var customerId = this.fetchCustomerId(isLocal),
      billId = ' ';
    if (!isLocal) {
      var accountDetails = this.fetchBillId();
      if (accountDetails) {
        billId = accountDetails.barId;
      }
    } else {
      billId = HelperConstants.HelperConstantsConfigs.LocalBillId;
    }
    let fetchStatus = {
      customerId: customerId,
      billId: billId,
      appendURL: isLocal
        ? HelperConstants.HelperConstantsConfigs.LocalhostUrl
        : '',
      get url() {
        let url = APIEndPoints.ApiEndPointConfigs.DirectDebitStatus.getPrimaryContact
          .replace('{customerId}', customerId)
          .replace('{billId}', billId);
        return this.appendURL + url;
      }
    };
    return this.axios.get(fetchStatus.url, { params: { sc: 'SS' } });
  }
  getSavedPaymentMethods(isLocal) {
    var personId = '',
      customerId = this.fetchCustomerId(isLocal),
      accountDetails = this.fetchPersonId();
    if (accountDetails) {
      personId = accountDetails.personId;
    } else {
      personId = HelperConstants.HelperConstantsConfigs.LocalBillId;
    }
    let fetchSavedPaymentsURL = {
      appendURL: isLocal
        ? HelperConstants.HelperConstantsConfigs.LocalhostUrl
        : '',
      get url() {
        let url = APIEndPoints.ApiEndPointConfigs.Wallet.getPayMeans
          .replace('{customerId}', customerId)
          .replace('{personId}', personId);
        return this.appendURL + url;
      }
    };
    return this.axios.get(fetchSavedPaymentsURL.url, {
      params: { _: new Date().getTime() }
    });
  }

  fetchServices(isLocal) {
    var customerId = this.fetchCustomerId(isLocal);
    let fetchServices = {
      customerId: customerId,
      appendURL: isLocal
        ? HelperConstants.HelperConstantsConfigs.LocalhostUrl
        : '',
      get url() {
        let url = APIEndPoints.ApiEndPointConfigs.DirectDebitStatus.getBillingEntity.replace(
          '{customerId}',
          customerId
        );
        return this.appendURL + url;
      }
    };
    return this.axios.get(fetchServices.url);
  }
  fetchCustomerId(isLocal) {
    if (!isLocal) {
      if (window.sessionStorage) {
        let topics = new SessionStorageTopics();
        var customerDetails = sessionStorage.getItem(
          topics.SessionStorageTopicConfigs.DirectDebitStatus.getCustomerId
        );
        try {
          customerDetails = JSON.parse(customerDetails);
        } catch (e) {
          customerDetails = null;
        }
        if (customerDetails) {
          return customerDetails.ID;
        } else {
          return null;
        }
      } else {
        return null;
      }
    } else {
      return HelperConstants.HelperConstantsConfigs.LocalCustomerId;
    }
  }

  fetchBillId() {
    if (window.sessionStorage) {
      let topics = new SessionStorageTopics();
      var customerDetails = sessionStorage.getItem(
        topics.SessionStorageTopicConfigs.DirectDebitStatus.getBillId
      );
      try {
        customerDetails = JSON.parse(customerDetails);
      } catch (e) {
        customerDetails = null;
      }
      if (customerDetails) {
        return customerDetails;
      } else {
        return null;
      }
    } else {
      return null;
    }
  }

  fetchPersonId() {
    if (window.sessionStorage) {
      let topics = new SessionStorageTopics();
      var customerDetails = sessionStorage.getItem(
        topics.SessionStorageTopicConfigs.DirectDebitStatus.getPersonId
      );
      try {
        customerDetails = JSON.parse(customerDetails);
      } catch (e) {
        customerDetails = null;
      }
      if (customerDetails) {
        return customerDetails;
      } else {
        return null;
      }
    } else {
      return null;
    }
  }
}
