import React, { Component } from 'react';

import Styles from './styles';

import { OptusButton, Heading, Anchor, TextField, Modal, DynamicScreen } from '../../lib-web';

class SimReplacement extends Component {
  constructor() {
    super();

    this.state = {
        steps: [{
            'index':0,
            'complete': false,
            'title':'Verify your identity',
            'nextStep':1,
            'inputPlaceholder':'Enter your phone number',
        }, {
            'index':1,
            'complete': false,
            'title':'Enter the code',
            'nextStep':2,
            'inputPlaceholder':'Enter the code',
        }, {
            'index':2,
            'complete': false,
            'title':'Enter your SIM number',
            'nextStep':'complete',
            'inputPlaceholder':'Enter your SIM number'
        }, {
            'index':3,
            'complete': false,
            'title':'Complete',
            'nextStep':null,
            'inputPlaceholder':null,
            'submitUrl':null,
        }],
        activeStep: 0,
        hideDots: false,
        validPhone: true,
        phone: '',
        validOTP: true,
        otp: '',
        validSimNumber: true,
        simNumber: ''
    };

    this.validatePhone = this.validatePhone.bind(this);
    this.validateOTP = this.validateOTP.bind(this);
    this.validateSimNumber = this.validateSimNumber.bind(this);
  }

  submitPhoneNumber() {
      if (this.state.phone === '') return;
      this.props.submitPhoneNumber({input: this.state.phone}).then((status) => {
          this.setState({ 'activeStep': 1});
      }).catch((status) => {
        if (status.status === 'MSISDNNotFoundSoft') {
            this.setState({
                validPhone: false
            });
        } else {
            this.setState({
                validPhone: true
            });
        }
        console.log(['errored input', status]);
      });
  }

  validatePhone(event) {
      console.log(['input', event]);
      this.setState({
          phone: event.target.value
      });
  }

  submitOTP() {
    if (!this.state.validOTP || this.state.otp === '') return;
    this.props.submitOTP({input: this.state.otp}).then((status) => {
        this.setState({ 'activeStep': 2});
    }).catch((status) => {
        console.log(['errored input', status]);
    });
  }

validateOTP(event) {
    this.setState({
        validOTP: this.props.validateAlphanumeric(event.target.value),
        otp: event.target.value
    });
}

submitSimNumber() {
    if (!this.state.validSimNumber || this.state.simNumber === '') return;
    this.props.submitSimNumber({input: this.state.simNumber}).then((status) => {
        this.setState({ 'activeStep': 3});
    }).catch((status) => {
        console.log(['errored input', status]);
    });
}

validateSimNumber(event) {
    this.setState({
        validSimNumber: this.props.validateAlphanumeric(event.target.value),
        simNumber: event.target.value
    });
}

  render() {
    return (
      <div>
        {!this.props.errorDetails ? '' : (<Modal
            open={!!this.props.errorDetails}
            onClose={() => { this.props.showError(null)}}
            >
            <Styles.CenteredWrapper>
                <Styles.ErrorIcon></Styles.ErrorIcon>
                <Styles.ModalHeading>Something went wrong</Styles.ModalHeading>
                <Styles.ModalText>{this.props.errorDetails.Description ? this.props.errorDetails.Description : this.props.errorDetails}</Styles.ModalText>
                {!this.props.errorDetails.CTA ? '' : (<>
                    <Styles.ModalCTA onClick={() => { window.location.href = this.props.errorDetails.URL}}>{this.props.errorDetails.CTA}</Styles.ModalCTA></>)}
            </Styles.CenteredWrapper>
        </Modal>)}
        <Anchor onClick={() => this.state.activeStep === 0 ? this.props.backFromSimReplacement() : this.setState({ 'activeStep': this.state.activeStep - 1})}>&lt; Back to previous page</Anchor>
        <Styles.Dots>{!this.state.hideDots && this.state.steps.map((o, i) => { return (i >= 3 ? <div key={i}></div> : <Styles.Dot key={i} active={this.state.activeStep >= i+1}>{i+1}</Styles.Dot>); })}</Styles.Dots>
        <Styles.Title>{this.state.steps[this.state.activeStep] ? this.state.steps[this.state.activeStep].title : ''}</Styles.Title>
        <DynamicScreen isVisible={0 === this.state.activeStep}>
            <p>Enter your phone number and we'll send you your one-time password:</p>
            <TextField error={!this.state.validPhone} onChange={this.validatePhone} placeholder={this.state.steps[0].inputPlaceholder}></TextField>
            {this.state.validPhone ? '' : (<Styles.ErrorText>Your phone number was incorrect, please try again.</Styles.ErrorText>)}
            <OptusButton onClick={() => { this.submitPhoneNumber(this.state.steps[0].nextStep, 0)} }>{this.props.loadingStatus ? 'Please wait' : 'Send me the code' }</OptusButton>
            <Styles.Divider></Styles.Divider>
            <p><strong>Was your phone stolen?</strong></p>
            <p>If your phone has been lost or stolen and you don't have an <Anchor href="https://www.optus.com.au/my-account">Optus account</Anchor> you will need to visit an Optus store.</p>
        </DynamicScreen>
        <DynamicScreen isVisible={1 === this.state.activeStep}>
            <p>We've texted a six-digit one-time password to <strong>{this.props.storedPhone}</strong>.</p>
            <TextField error={!this.state.validOTP} value={this.state.otp} onChange={this.validateOTP} placeholder={this.state.steps[1].inputPlaceholder}></TextField>
            {this.state.validOTP ? '' : (<Styles.ErrorText>Enter your OTP</Styles.ErrorText>)}
            <OptusButton onClick={() => { this.submitOTP(this.state.steps[1].nextStep, 1)} }>{this.props.loadingStatus ? 'Please wait' : 'Confirm your OTP' }</OptusButton>
            <Styles.Divider></Styles.Divider>
            <p>Haven't received the code? <Anchor onClick={() => this.submitPhoneNumber(this.state.steps[0].nextStep, 0)}>Re-send it.</Anchor></p>
        </DynamicScreen>
        <DynamicScreen isVisible={2 === this.state.activeStep}>
            <p>Please enter the code found on your replacement SIM to activate it.</p>
            <TextField error={!this.state.validSimNumber} value={this.state.simNumber} onChange={this.validateSimNumber} placeholder={this.state.steps[2].inputPlaceholder}></TextField>
            {this.state.validSimNumber ? '' : (<Styles.ErrorText>Enter your SIM number</Styles.ErrorText>)}
            <OptusButton onClick={() => { this.submitSimNumber(this.state.steps[2].nextStep, 2)} }>{this.props.loadingStatus ? 'Please wait' : 'Activate SIM' }</OptusButton>
            <Styles.Divider></Styles.Divider>
            <p>Your activation code is located on the back of your new SIM.</p>
        </DynamicScreen>
        <DynamicScreen isVisible={3 === this.state.activeStep}>
            <Styles.CenteredWrapper>
                <Styles.SuccessTick></Styles.SuccessTick>
                <Heading>Thank you!</Heading>
                <p><strong>The activation is being processed.<br />Your temporary order number is {this.props.toExtended}.</strong></p>
                <p>It usually takes between 15-20 minutes to process. In rare cases the activation can take up to 4 hours.</p>
            </Styles.CenteredWrapper>
        </DynamicScreen>
      </div>
    );
  }
}

export default SimReplacement;